import React, { useState, useEffect, useRef } from 'react';
import { saveAs } from 'file-saver';

const cumminsBlue = '#00489C';
const cumminsRed = '#D94036';
const lightGray = '#E0E0E0';
const darkGray = '#A0A0A0';

const inputStyle = {
  width: '100%',
  padding: '4px',
  fontSize: '0.9rem',
  textAlign: 'center',
  border: '1px solid #ccc',
  borderRadius: '4px',
  boxSizing: 'border-box',
};

const headerStyle = {
  fontSize: '0.9rem',
  fontWeight: 'bold',
  textAlign: 'center',
  padding: '6px',
  backgroundColor: cumminsBlue,
  color: 'white',
  border: '1px solid white',
};

const tableStyle = {
  width: '100%',
  borderCollapse: 'collapse',
  border: '2px solid #ccc',
};

const rowStyle = {
  padding: '8px 0',
};

const titleStyle = {
  color: 'black',
  margin: 0,
  fontSize: '1.8rem',
  fontFamily: '"Open Sans", sans-serif',
  fontStyle: 'italic',
  fontWeight: 'bold',
  textDecoration: 'underline',
  textDecorationColor: '#FFD700',
  textDecorationThickness: '3px',
  paddingBottom: '15px',
  textUnderlineOffset: '6px'
};

const textAreaStyle = {
  ...inputStyle,
  width: '300px',
  height: '60px',
  padding: '1px',
  fontSize: '11px',
};

const CumminsFollowUpApp = () => {
    const [rows, setRows] = useState(() => {
      const savedRows = localStorage.getItem('rows');
      return savedRows ? JSON.parse(savedRows) : Array.from({ length: 12 }, (_, index) => ({
        id: index + 1,
        staff: '',
        date: '',
        customerName: '',
        customerPhone: '',
        day1: { call1: '', call2: '', text: '', fqr: '' },
        day2: { call1: '', call2: '', text: '', email: '' },
        day3: { call1: '', call2: '', text: '', email: '' },
        count: 0,
        score: '0%',
        notes: ''
      }));
    });
  
  const [clients, setClients] = useState([]);
  const [selectedClient, setSelectedClient] = useState('');

  useEffect(() => {
    localStorage.setItem('rows', JSON.stringify(rows));
  }, [rows]);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + '/clients.json')
      .then(response => response.json())
      .then(data => setClients(data))
      .catch(error => console.error('Error loading clients:', error));
  }, []);

  const tableRef = useRef(null);

  const handleChange = (id, category, field, value) => {
    setRows(rows.map(row => {
      if (row.id === id) {
        let updatedRow = { ...row };
        if (category.startsWith('day')) {
          updatedRow[category][field] = value;
        } else {
          updatedRow[category] = value;
        }
        const { count, score } = calculateCountAndScore(updatedRow);
        return { ...updatedRow, count, score };
      }
      return row;
    }));
  };

  const handleClearAll = () => {
    localStorage.removeItem('rows');
    setRows(rows.map(row => ({
      ...row,
      staff: '',
      date: '',
      customerName: '',
      customerPhone: '',
      day1: { call1: '', call2: '', text: '', fqr: '' },
      day2: { call1: '', call2: '', text: '', email: '' },
      day3: { call1: '', call2: '', text: '', email: '' },
      count: 0,
      score: '0%',
      notes: ''
    })));
  };

  const calculateCountAndScore = (row) => {
    const dayFields = ['day1', 'day2', 'day3'];
    let count = 0;
    dayFields.forEach(day => {
      Object.values(row[day]).forEach(value => {
        count += value === '1' ? 1 : 0;
      });
    });
    const score = Math.round((count / 12) * 100) + '%';
    return { count, score };
  };

  const pasteDataFromReport = async () => {
    try {
      const text = await navigator.clipboard.readText();
      const parsedRows = JSON.parse(text);
      setRows(parsedRows);
    } catch (err) {
      console.error('Failed to read data from clipboard: ', err);
    }
  };

  const generateHTMLReport = (rows) => {
    const generateHTMLContent = (currentRows, clientName) => `
<!DOCTYPE html>
<html lang="en">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>${clientName} - 72 HOUR FOLLOW-UP ANALYSIS</title>
    <style>
        body {
            font-family: Arial, sans-serif;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: center;
            min-height: 100vh;
        }
        .container {
            width: 100%;
            max-width: 1300px;
            padding: 20px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 20px;
        }
        .title {
            color: black;
            font-size: 1.8rem;
            font-style: italic;
            font-weight: bold;
            text-decoration: underline;
            text-decoration-color: #FFD700;
            text-decoration-thickness: 3px;
            padding-bottom: 15px;
            text-underline-offset: 6px;
            text-align: center;
            flex-grow: 1;
        }
        .logo {
            height: 100px;
            flex-shrink: 0;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 2px solid #ccc;
        }
        th, td {
            border: 1px solid #ccc;
            padding: 8px;
            text-align: center;
        }
        th {
            background-color: #00489C;
            color: white;
        }
        input, textarea {
            padding: 4px;
            box-sizing: border-box;
        }
        .staff-input, .customer-info input {
            width: 120px;
        }
        .day-input, .count-input, .score-input {
            width: 100%;
        }
        .notes textarea {
            width: 200px;
            height: 60px;
        }
        .btn-container {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-top: 20px;
        }
        .btn {
            padding: 10px 20px;
            color: white;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 0.8rem;
            font-weight: bold;
        }
        .btn-read {
            background-color: #00489C;
        }
        .footer {
            margin-top: 20px;
            font-size: 0.7rem;
            color: #00489C;
            text-align: center;
            border-top: 1px solid #ccc;
            padding: 10px;
            width: 100%;
        }
    </style>
</head>
<body>
    <div class="container">
        <div class="header">
            <img src="data:image/png;base64," alt="Cummins Performance Group Logo" class="logo">
            <h1 class="title">${clientName} - 72 HOUR FOLLOW-UP ANALYSIS</h1>
            <img src="data:image/png;base64," alt="Real Deal Report" class="logo" style="transform: rotate(-5deg);">
        </div>
        
        <table id="followUpTable">
            <thead>
                <tr>
                    <th>STAFF</th>
                    <th>DATE</th>
                    <th>CUSTOMER</th>
                    <th colspan="4">DAY 1</th>
                    <th colspan="4">DAY 2</th>
                    <th colspan="4">DAY 3</th>
                    <th colspan="2">SCORE</th>
                    <th>NOTES</th>
                </tr>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>CALL</th>
                    <th>CALL</th>
                    <th>TEXT</th>
                    <th>FQR</th>
                    <th>CALL</th>
                    <th>CALL</th>
                    <th>TEXT</th>
                    <th>EMAIL</th>
                    <th>CALL</th>
                    <th>CALL</th>
                    <th>TEXT</th>
                    <th>EMAIL</th>
                    <th>COUNT</th>
                    <th>SCORE</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                ${currentRows.map((row, index) => `
                    <tr>
                        <td><input type="text" class="staff-input" value="${row.staff}" oninput="updateRow(${index}, 'staff', this.value)"></td>
                        <td><input type="date" value="${row.date}" oninput="updateRow(${index}, 'date', this.value)"></td>
                        <td class="customer-info">
                            <input type="text" value="${row.customerName}" placeholder="Customer Name" oninput="updateRow(${index}, 'customerName', this.value)">
                            <input type="text" value="${row.customerPhone}" placeholder="Phone Number" oninput="updateRow(${index}, 'customerPhone', this.value)">
                        </td>
                        ${['day1', 'day2', 'day3'].flatMap(day => 
                            Object.entries(row[day]).map(([field, value]) => 
                                `<td><input type="text" class="day-input" value="${value}" oninput="updateRow(${index}, '${day}-${field}', this.value)"></td>`
                            ).join('')
                        ).join('')}
                        <td><input type="text" class="count-input" value="${row.count}" readonly></td>
                        <td><input type="text" class="score-input" value="${row.score}" readonly></td>
                        <td class="notes"><textarea oninput="updateRow(${index}, 'notes', this.value)">${row.notes}</textarea></td>
                    </tr>
                `).join('')}
            </tbody>
        </table>
        
        <div class="btn-container">
            <button onclick="copyReportData()" class="btn btn-read">Copy Data from Report</button>
        </div>
        
        <div class="footer">
            © Cummins Performance Group - All rights reserved
        </div>
    </div>

    <script>
        let rows = ${JSON.stringify(rows)};

        function updateRow(index, category, value) {
            if (category === 'staff' || category === 'date' || category === 'customerName' || category === 'customerPhone' || category === 'notes') {
                rows[index][category] = value;
            } else {
                const [day, field] = category.split('-');
                rows[index][day][field] = value;
            }
            const { count, score } = calculateCountAndScore(rows[index]);
            rows[index].count = count;
            rows[index].score = score;
            document.querySelector(\`#followUpTable tbody tr:nth-child(\${index + 1}) .count-input\`).value = count;
            document.querySelector(\`#followUpTable tbody tr:nth-child(\${index + 1}) .score-input\`).value = score;
        }

        function calculateCountAndScore(row) {
            const dayFields = ['day1', 'day2', 'day3'];
            let count = 0;
            dayFields.forEach(day => {
                Object.values(row[day]).forEach(value => {
                    count += value === '1' ? 1 : 0;
                });
            });
            const score = Math.round((count / 12) * 100) + '%';
            return { count, score };
        }

        function copyReportData() {
            navigator.clipboard.writeText(JSON.stringify(rows)).then(() => {
                alert('Data copied to clipboard');
            }).catch(err => {
                console.error('Failed to copy data: ', err);
            });
        }
    </script>
</body>
</html>
  `;

    const htmlContent = generateHTMLContent(rows, selectedClient);
  
    const blob = new Blob([htmlContent], { type: 'text/html' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'report.html';
    a.click();
    URL.revokeObjectURL(url);
  };

  return (
    <div style={{ padding: '5px', maxWidth: '1300px', margin: '0 auto', fontFamily: 'Arial, sans-serif' }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '5px' }}>
        <img src="./logo.png" alt="Cummins Performance Group Logo" style={{ height: '100px' }} />
        <h1 style={titleStyle}>{selectedClient ? `${selectedClient} - ` : ''}72 HOUR FOLLOW-UP ANALYSIS</h1>
        <img src="./rdp.png" alt="Real Deal Report" style={{ height: '100px', transform: 'rotate(-5deg)' }} />
      </div>
      
      <div style={{ marginBottom: '20px' }}>
        <label htmlFor="client-select">Choose a client: </label>
        <select id="client-select" value={selectedClient} onChange={(e) => setSelectedClient(e.target.value)} style={inputStyle}>
          <option value="" disabled>Select a client</option>
          {clients.map(client => (
            <option key={client.id} value={client.name}>{client.name}</option>
          ))}
        </select>
      </div>
      
      <div ref={tableRef}>
        <table style={tableStyle}>
          <thead>
            <tr>
              <th style={headerStyle}>STAFF</th>
              <th style={headerStyle}>DATE</th>
              <th style={headerStyle}>CUSTOMER</th>
              <th colSpan="4" style={headerStyle}>DAY 1</th>
              <th colSpan="4" style={headerStyle}>DAY 2</th>
              <th colSpan="4" style={headerStyle}>DAY 3</th>
              <th colSpan="2" style={headerStyle}>SCORE</th>
              <th style={headerStyle}>NOTES</th>
            </tr>
            <tr style={{ backgroundColor: darkGray, color: 'white', fontSize: '0.7rem' }}>
              <th></th>
              <th></th>
              <th></th>
              <th>CALL</th>
              <th>CALL</th>
              <th>TEXT</th>
              <th>FQR</th>
              <th>CALL</th>
              <th>CALL</th>
              <th>TEXT</th>
              <th>EMAIL</th>
              <th>CALL</th>
              <th>CALL</th>
              <th>TEXT</th>
              <th>EMAIL</th>
              <th>COUNT</th>
              <th>SCORE</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {rows.map((row, index) => (
              <tr key={row.id} style={{ ...rowStyle, backgroundColor: index % 2 === 0 ? lightGray : 'white' }}>
                <td>
                  <input
                    type="text"
                    style={{
                      ...inputStyle,
                      width: '150px',
                    }}
                    value={row.staff}
                    onChange={(e) => handleChange(row.id, 'staff', null, e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="date"
                    style={inputStyle}
                    value={row.date}
                    onChange={(e) => handleChange(row.id, 'date', null, e.target.value)}
                  />
                </td>
                <td>
                  <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <input
                      type="text"
                      placeholder="Customer Name"
                      style={{ ...inputStyle, width: '150px', marginBottom: '0px' }}
                      value={row.customerName}
                      onChange={(e) => handleChange(row.id, 'customerName', null, e.target.value)}
                    />
                    <input
                      type="text"
                      placeholder="Phone Number"
                      style={{ ...inputStyle, width: '150px' }}
                      value={row.customerPhone}
                      onChange={(e) => handleChange(row.id, 'customerPhone', null, e.target.value)}
                    />
                  </div>
                </td>
                {['day1', 'day2', 'day3'].flatMap(day => 
                  Object.entries(row[day]).map(([field, value]) => (
                    <td key={`${day}-${field}`}>
                      <input
                        type="text"
                        style={inputStyle}
                        value={value}
                        onChange={(e) => handleChange(row.id, day, field, e.target.value)}
                      />
                    </td>
                  ))
                )}
                <td>
                  <input type="text" style={inputStyle} value={row.count} readOnly />
                </td>
                <td>
                  <input
                    type="text"
                    style={{ ...inputStyle, width: '48px', marginBottom: '0px' }}
                    value={row.score}
                    readOnly
                  />
                </td>
                <td>
                  <textarea
                    style={textAreaStyle}
                    value={row.notes}
                    onChange={(e) => handleChange(row.id, 'notes', null, e.target.value)}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      <div style={{ marginTop: '20px', display: 'flex', justifyContent: 'space-between' }}>
        <button 
          onClick={handleClearAll} 
          style={{ 
            padding: '10px 20px', 
            backgroundColor: cumminsBlue, 
            color: 'white', 
            border: 'none', 
            borderRadius: '5px', 
            cursor: 'pointer',
            fontSize: '0.8rem',
            fontWeight: 'bold',
          }}
        >
          Clear All Fields
        </button>
        
        <button 
          onClick={() => generateHTMLReport(rows)}
          style={{ 
            padding: '10px 20px', 
            backgroundColor: 'green', 
            color: 'white', 
            border: 'none', 
            borderRadius: '5px', 
            cursor: 'pointer',
            fontSize: '0.8rem',
            fontWeight: 'bold',
          }}
        >
          Generate HTML Report
        </button>
        
        <button 
          onClick={pasteDataFromReport}
          style={{ 
            padding: '10px 20px', 
            backgroundColor: cumminsRed, 
            color: 'white', 
            border: 'none', 
            borderRadius: '5px', 
            cursor: 'pointer',
            fontSize: '0.8rem',
            fontWeight: 'bold',
          }}
        >
          Paste Data from Report
        </button>
      </div>
      
      <div style={{ marginTop: '20px', fontSize: '0.7rem', color: cumminsBlue, textAlign: 'center', borderTop: '1px solid #ccc', padding: '10px' }}>
        © Cummins Performance Group - All rights reserved
      </div>
    </div>
  );
};

export default CumminsFollowUpApp;
